@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap);
body {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Roboto',  sans-serif;
    font-size: 16px;
}
a {
    text-decoration: none;
}



.app-wrapper {

    display: flex;
    flex-flow: column;
    justify-content: space-between;
    padding:0;
    box-sizing: border-box;

}

.logo-wrapper {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  height: 100px;
  margin-top: 60px;
  border-bottom: 1px solid black;
}
.logo-wrapper img {
  width: 80px;
  height: 80px;
  margin-left: 17px;
}
.center-part-wrapper {
  margin-top: 25px;
    font-size: 20px;
    display: flex;
    flex-flow: row;
    justify-content: space-between;
}
.logo-wrapper .center-part {
 
}
.logo-wrapper h1 {
  opacity: 0;
  margin: 0;
  position: relative;
  left: 30px;
 
}
.logo-wrapper .first-part-logo {
  margin-top: -26px;
  margin-right: 10px;
  font-size: 26px;
  font-family: Arial, Helvetica, sans-serif, cursive;
}
.letter {
  display:  inline-block;
    opacity: 0;
}
.letter-div {
  display: inline-block;
}
.navigation-wrapper {
   
    position: fixed;
    z-index: 999;
    display: flex;

    flex-flow: row wrap;
    justify-content:space-between ;
    width:  100%;
    height: 60px;
    
    background: #e6e6e6;
    transition:  height 2s ease;
   
    
}
.navigation-wrapper ul {
    margin-top: 21px;
    padding: 0px 10px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    list-style: none;
}
.navigation-wrapper .left-part-navigation {
    width:  78%;
}

.navigation-wrapper .right-part-navigation {
    width: 18%;
    
}

.navigation-wrapper a {
    
    font-size: 16px;
    font-weight: lighter;
    color: black;
}
.navigation-wrapper a:hover {
    color: lightgray;
}

.drop-lines-responsive{
    display: none;
    position: absolute;
    top: 10px;
    right: 10px;
    border: 2px solid #fff;
    border-radius: 2px;
    width: 40px;
    height: 40px;
    text-align: center;
}
.drop-lines-responsive:hover {
    cursor: pointer;
}
.drop-lines-responsive span {
   
    display: block;
    background: #fff;
    height: 4px;
    margin: 7px;
    width: 70%;
    border-radius: 3px;
}


.dropdown-wrapper {
    display: flex;
    flex-flow: column;
    align-content: space-between;
    position: absolute;
    background-color: #fff;
    border: 1px solid black;
    height: auto;
    width: 100%;
    left: 0;
    top: -435px;
    height: 428px;
    opacity: 0;
  }


  
  .content-img-wrapper {
    width: 100%;
    display: flex;
    flex-flow: row;
    margin: 20px;
    padding: 0;
    justify-content: space-around;
  }
  .content-img-wrapper .section {
    width: 25%;
  }
  .content-img-wrapper  hr {
    width: 200px;
    background-color: grey;
  }
  .content-img-wrapper .section {
    height: 75%;
    margin-top: 40px;
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  .content-img-wrapper .section h3 {
    font-weight: 100;
    margin: 0;
  }
  .content-img-wrapper ul {
    margin: auto;
    display: flex;
    flex-flow: column;
  }
  .content-img-wrapper ul li {
    margin-top: 10px;
  }
  .dropdown-wrapper h1 {
    position: absolute;
    margin: 0;
    left: 28%;
    top: 10px;
  }
  .dropdown-wrapper img {
    width: 530px;
    height: 356px;
    background-color: #e6e6e6;
    border: 1px solid black;
    padding: 4px;
    border-radius: 20px;
    object-fit: cover;
  }
  .dropdown-wrapper button {
    position: relative;
    left: 28%;
    bottom: 25px;
    width: 110px;
    padding: 8px;
    background-color: rgb(80, 188, 221);
    color: #fff;
    border: none;
    border-radius: 5px;
  }
  .dropdown-wrapper button:hover {
    cursor: pointer;
  }
  

.dropdown-wrapper h1{
    left: 23%;
}

.nav-mobile {
    position: fixed;
    z-index: 10;
    top: 55px;
    left: 0;
    background: #e6e6e6;
    width: 100%;
}
.nav-mobile a {
    color: black;
    display: block;
}
.nav-mobile ul {
    padding: 0;
    list-style: none;
    margin: 20px auto;
    padding: 0;
    list-style: none;
    margin: 20px auto;
    display: flex;
    flex-flow: column;
    align-items: center;
}
.nav-mobile ul li {
    width: 100%;
    margin:  4px;
    padding: 9px;
    text-align: center;
}
.nav-mobile ul li:hover {
    background-color: #fff;
}

.home-wrapper {
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    
}
.upper-part-home {
    display: flex;
    flex-flow: row;
    justify-content: space-around;
}
.home-content-wrapper {
    width: 40%;
    display: flex;
    flex-flow: column;
    align-items: center;
    margin-left:22px;
}
.home-content-wrapper h2 {
    margin: 0;
    margin-top: 28px;
}
.home-image-wrapper {
    width: auto;
    margin-top: 70px;
}
.home-image-wrapper img {
    width: 560px;
    height: 450px;
    padding:10px;
    border: 2px solid black;

}
.home-slider-wrapper {
    width: 80%;
}
.down-part-home {
    width: 100%;
    

}
.slider-framework{
    width: 70%;
    height: 450px;
    margin: 40px auto;
    display: flex;
    flex-flow: row;
    border: 2px solid black;
}
.main-framework {
    margin: 20px auto;
    width: 90%;
    background-color: rgb(196, 207, 41);
    border: 10px outset#dbdd2e;
    text-align: center;
}
.new-slider-container {
  border: 1px solid black;
  width: 90%;
  height: 450px;
  margin: 40px auto;
  display: flex;
  flex-flow: column;
  align-items: center;
}
.new-slider-container h1 {
  margin: 0;
  width: 100%;
  background-color:black;
  color: white;
  text-align: center;
}
.new-slider-container p {
  width: 100%;
  margin: 0;
  padding: 5px 0;
  color: white;
  background-color: black;
  text-align: center;
}
.new-slider-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
}
.new-slider {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  width: 100%;
  overflow: hidden;
}
.right-direction {
  background: none;
  border: none;
  position: absolute;
  z-index: 1;
  right: 5%;
  width: 7%;
  min-height: 411px;
  outline: none;
}
.left-direction {
  background: none;
  border: none;
  position: absolute;
  z-index: 1;
  left: 5%;
  width: 7%;
  min-height: 411px;
  outline: none;
}
.left-direction:hover,
.right-direction:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.37);
}
.single-img {
  min-width: 33.33%;
  transition: 0.7s;
}

.img-wrapper {
  border: 1px solid black;
  margin: 3px;
  padding: 8px;
  width: auto;
  height: 94%;
  transform: scale(0.5);
}

.img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
button i {
  font-size: 30px;
  color:white;
  
}

.slider-mobile-wrapper {
    display: none;
}
.swiper-container-horizontal > .swiper-pagination-bullets {
    position: relative;
    top:-4px;
   
}
.footer-wrapper{
    display: flex;
    flex-flow: row;
    align-items: center;
    background:#5c5a55;
    border-top: 2px solid #878787;
}
.footer-wrapper h3 {
    margin: 0;margin-bottom: 10px;
}
.footer-wrapper p {
    margin-left:10px;
}
.footer-wrapper ul {
    margin: 0;
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    list-style: none;
}
.footer-wrapper a {
    font-size: 12px;
    color: #fff;
    
}
.footer-link-wrapper {
    width: 100%;
}
.footer-wrapper a:hover {
    text-decoration: underline;
}


.contacts-wrapper {
    background-color: #e6e6e6;
    border: 1px solid #000;
    margin: 41px auto; 
    width: 70%;
    
   
    justify-content: space-around;
}
.contacts-wrapper h1 {
    margin: 0;
    margin-top: 10px;
}

.content-contact-wrapper {
    width:45%;
    margin: 11% auto;
    display: flex;
    flex-flow: column;
    
}
.content-contact-wrapper section {
    margin-bottom: 15px;
    height: 100%;
}
.content-contact-wrapper ul{

    display: flex;
    flex-flow: column;
    margin: 0;
    padding: 0;
    list-style: none;
    

}
.content-contact-wrapper ul li {
    margin-top: 15px;
    font-size: 16px;
}
.content-contact-wrapper ul hr {
    width: 200px;
    background-color: grey;
}
.content-contact-wrapper section p {
    margin: 0;
}
.img-container{
    width: 42%;
   margin: 11% auto;
}
.img-container img {
    width: 100%;
    padding: 5px;
    border: 1px solid #000;
}


.clothes-wrapper {
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.clothes-single{
    width: 25%;
    min-height: 216px;
    margin: 30px 40px;
    border: 1px solid black;
    display:flex;
    flex-flow:column;
    align-content:space-between ;

}
.clothes-single img {
    width: 100%;
    
}
.accesories-wrapper {
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.accesories-single{
    width: 25%;
    margin: 30px 40px;
    border: 1px solid black;
    display:flex;
    flex-flow:column;
    align-content:space-between ;

}
.accesories-single img {
    width: 100%;
}
@media all and (max-width: 1024px) {
  
  .upper-part-home {
    margin-bottom: 30px;
    width: 100%;
  }
  .navigation-wrapper {
    height: 63px;
  }
  .navigation-wrapper a {
    font-size: 18px;
  }
  .navigation-wrapper .right-part-navigation {
    position: unset;
  }
  .logo-wrapper {
    height: 120px;
  }
  .logo-wrapper img {
    width: 100px;
    height: 100px;
    margin-top: 5px;
  }
  .logo-wrapper .first-part-logo {
    font-size: 30px;
  }
  .logo-wrapper h1 {
    top: 4px;
  }
  .dropdown-wrapper {
    border-left: none;
    border-right: none;
  }
  .dropdown-wrapper img {
    width: 430px;
    height: 350px;
    margin-right: 30px;
    margin-top: 15px;
  }
  .dropdown-wrapper h1 {
    left: 17%;
  }
  .content-img-wrapper .section {
    margin-top: 55px;
    font-size: 20px;
  }
  .dropdown-wrapper .close-button {
    bottom: 50px;
    left: 23%;
    font-size: 20px;
    padding: 10px;
  }
  .upper-part-home {
    flex-flow: column;
    align-items: center;
  }
  .home-content-wrapper {
    margin: 0;
    width: 80%;
  }
  .home-content-wrapper h2 {
    margin-top: 15px;
  }
  .home-content-wrapper p {
    margin-bottom: 0;
    font-size: 22px;
  }
  .home-image-wrapper {
    margin-top: 30px;
    text-align: center;
    width: 100%;
  }
  .home-image-wrapper img {
    width: 75%;
    height: auto;
  }
  .new-slider-container {
    display: none;
    margin-top: 50px;
    margin-bottom: 55px;
    width: 76%;
    height: 100%;
  }

  .new-slider-container .left-direction {
    left: 13%;
  }
  .slider-mobile-wrapper {
    display: block;
    margin-bottom: 30px;
    width: 100%;
  }
  .swiper-container {
    width: 78%;
  }
  .img-wrapper {
    border: 2px solid black;
    padding: 12px;
  }
  .single-img {
    min-width: 100%;
    transform: translateX(0%);
  }
  .new-slider-container i {
    color: black;
    font-size: xxx-large;
  }
  .clothes-single,
  .accesories-single {
    width: 35%;
  }

  .clothes-single img,
  .accesories-single img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .contacts-wrapper {
    flex-flow: column;
    align-items: center;
    min-height: 80vh;
  }
  
  .img-container{
    width: 90%;
  }
}


@media all and (max-width: 823px) {
  .logo-wrapper {
    height: auto;
  }
    .navigation-wrapper li a {
      font-size: 18px;
    }
    .left-part {
      width: 10%;
    }
    .center-part-wrapper {
      flex-flow: row wrap;
      justify-content: center;
    }
    
    .nav-mobile a {
      font-size: 20px;
    }
    .left-part-navigation,
    .right-part-navigation {
      display: none;
    }
    .dropdown-wrapper {
      top: -465px;
    }
    .drop-lines-responsive {
        right: 25px;
      display: block;
    }
    .new-slider-container button.right-direction {
      right: 8%;
    }
    .new-slider-container button.left-direction {
      left: 8%;
    }
    .dropdown-wrapper img {
      width: 40%;
      height: 71%;
      margin-top: 64px;
    }
    .dropdown-wrapper h1 {
      left: 35%;
    }
    .content-img-wrapper hr {
      width: 82%;
    }
    .content-img-wrapper .section {
      margin-top: 38px;
    }
    .nav-mobile ul li {
      margin: 0;
    }
    .dropdown-wrapper .close-button {
      font-size: 16px;
      bottom: 16px;
    }
    .contacts-wrapper {
      height: unset;
    }
    .footer-wrapper a {
       font-size: 13px;
    }
  }
 
@media all and (max-width: 600px ) {
    .navigation-wrapper {
        height: 55px;
    }
    .drop-lines-responsive {
        right: 10px;
        top: 6px;
    }
    .clothes-single,.accesories-single {
        width: 60%;
    }
    .contacts-wrapper {
        width: 90%;
        margin: 25px auto;
    }
    .content-contact-wrapper {
        width: 60%;
    }
    .content-contact-wrapper ul {
        margin: 0;
    }
    .content-contact-wrapper ul li {
        font-size: 20px;
    }
    .nav-mobile {
        border-bottom: 1px solid #000;
    }
   
    .footer-wrapper ul {
        flex-flow: column;
        margin-left: 25%;
        margin-top: 20px;
      }
      .footer-wrapper ul li {
        margin: 3px ;
      }
}
@media all and (max-width: 414px) {
  .navigation-wrapper {
    height: 57px;
  }
  .drop-line-responsive {
      top:5px
  }
  .center-part-wrapper {
    margin-top: 0;
  }
  .logo-wrapper {
      margin-top: 78px;
  }
  .logo-wrapper .first-part-logo {
    font-size: 22px;
    position: relative;
    top: -10px;
  }
  .logo-wrapper img {
    width: 60px;
    height: 60px;
  }
  .left-part,.right-part {
    position: relative;
    top: -11px;
  }
  .center-part-wrapper h1 {
    font-size: xx-large;
  }
  .swiper-container {
      width: 82%;
  }
  .img-wrapper img {
      margin-top: 2px;
  }
  .dropdown-wrapper h1 {
    margin-top: 5px;
      left:25%;
      font-size: 26px;
  }
  .content-img-wrapper {
     margin:0;
     
  }
  .dropdown-wrapper {
    justify-content: center;
    align-items: center;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
  }
  .dropdown-wrapper img{
   display: none;
  }
  .dropdown-wrapper .close-button{
    bottom:1px;
    left:0;
  }
  .footer-wrapper ul {
    flex-flow: column;
    margin-left: 5px;
    margin-top: 20px;
  }
  .footer-wrapper ul li {
    margin: 3px ;
  }

}

